import React from "react"
import ContentPage from "../components/ContentPage"
import RichText from "../components/RichText"
import TextGroup from "../components/TextGroup.js"
import Img from "gatsby-image"
//import { StaticQuery, graphql } from "gatsby"

const CourseDetails = ( {data} ) => (
    <ContentPage pageTitle = "Course Details">

        <RichText>

            <TextGroup>
                <p>
                    Jolong Park is a all in the one, purpose-built, state-of-the-art equestrian property, which is only a short 35-minute drive from Melbourne. Jolong Park features stables, clubhouse, a 2,000 metre sand training track and a 2,200 meter grass racetrack.
                </p>
                <p>
                    The event will take place on the 2,200 meter grass race track. The race distances will consist of full laps of the track, starting at a single lap for a 2.2km total distance, witha longer event of three laps for a 6.6km distance.
                </p>
            </TextGroup>

            <TextGroup>
                <h3>
                    Track Location:
                </h3>

                <p>
                    Jolong Park, 88-128 Riverend Rd, Bangholme, Victoria
                </p>
            </TextGroup>

            <TextGroup centered={false}>
                <h3>
                    Transport Options:
                </h3>

                <div className = "aside-group">
                    <h4>
                        Car
                    </h4>
                    <p>
                        Jolong Park is just off the Mornington Peninsula Freeway to the East or Eastlink to the West.
                        <br/>
                        Travel time is ~35 minutes from Melbourne CBD and ~15 minutes from Frankston.
                        <br/>
                        An on site car park will be provided free of charge.
                    </p>
                </div>

                <div className = "aside-group">
                    <h4>
                        Train
                    </h4>
                    <p>
                        Chelsea Station is a 3km from Chelsea Train Station on the Frankston Line.
                    </p>
                </div>
            </TextGroup>

            <TextGroup classes="vsa-center-text">
                <a href="https://www.google.com/maps/place/Jolong+Park/@-38.0428439,145.1374475,14.35z/data=!4m5!3m4!1s0x6ad612a8275a3ba3:0x2e9c092257cee2c2!8m2!3d-38.0430386!4d145.1474738">
                    Jolong Park on Google Maps
                </a>

                <StaticQuery
                    query={query}
                    render={data => (
                        <Img fluid={data.mapZoomedOut.childImageSharp.fluid} alt="" />
                    )}
                />

                <StaticQuery
                    query={query}
                    render={data => (
                        <Img fluid={data.mapZoomedIn.childImageSharp.fluid} alt="" />
                    )}
                />  

            </TextGroup>

        </RichText>
    </ContentPage>
)

export const mapImage = graphql`
  fragment mapImage on File {
    childImageSharp {
      fluid(maxWidth: 800) {
        ...GatsbyImageSharpFluid
      }
    }
  }
`

export const query = graphql`
  query {
    mapZoomedOut: file(relativePath: { eq: "map-jolong-zoomed-out.png" }) {
      ...mapImage
    }
    mapZoomedIn: file(relativePath: { eq: "map-jolong-zoomed-in.png" }) {
      ...mapImage
    }
  }
`

export default CourseDetails
